
import lrz from 'lrz'
import HeaderBack from '../Common/HeaderBack'
import Rater from 'vux/src/components/rater/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Range from 'vux/src/components/range/index.vue'


export default {
name: 'logistics',
components: {
myHeader: HeaderBack,
Rater,
Group,
Cell,
},
data() {
return {
header: {
HBL_icon: "icon_back",
HBC_text: "发表评价",
HBR_icon: "icon_edit",
HBR_show: true,
HBR_type: "addevaluate",
HBR_show_long: true,
HBR_text_long: "发布",
},
isSend: true,
curComment: 0,
orderId: '',
evaList: [],
}
},
methods: {
addMedia(index) {
this.curComment = index;
},
onFileChange(e) {
let files = e.target.files || e.dataTransfer.files;
if (!files.length) return;
this.createImage(files, e);
},
createImage: function (file, e) {
let self = this;
let i = this.curComment;

lrz(file[0], {width: 200,height: 200,fieldName:'file1'}).then(function (rst) {

let imgStr = rst.base64, imgStrSend = imgStr.slice(imgStr.indexOf(',') + 1);
self.evaList[i].imgList.push({img_url:imgStrSend});

return rst;
}).always(function () {

e.target.value = null;
});
},
deleteImg (index,i) {
this.evaList[index].imgList.splice(i,1);
},
sendEvaFn() {
this.isSend = true;
this.evaList.forEach((element, index, arr) => {
if (!element.star) {
this.isSend = false;
this.$vux.toast.show({
type: 'success',
text: '亲，评论星级不能为空哦~',
width: '16em'
});
} else if (element.comments.replace(/(^\s*)|(\s*$)/g, "") === '') {
this.isSend = false;
this.$vux.toast.show({
type: 'success',
text: '亲，评论内容不能为空哦~',
width: '16em'
});
}
});
if(!this.isSend)
return

let _evaList = [];
this.evaList.forEach ( (ele, i) => {
let o = {},a=[];
o.pro_id = ele.pro_id;
o.pro_name = ele.pro_name;
o.order_id = ele.order_id;
o.comments = ele.comments;
o.imgList = ele.imgList;
o.star = ele.star;
_evaList.push(o)
})

comTools.showLoading(this, '发布中...');
setTimeout((data) => {

comTools.hideLoading(this);
if (true) {
this.$vux.toast.show({
type: 'success',
text: '评论成功~',
width: '7.5em'
});
setTimeout(() => {
this.$router.go(-1);
}, 3000)
}
}, 500)
}

},
created() {
let ordObj = this.$store.state.orderObj;
console.log(ordObj);
ordObj.productList.forEach((element, index, arr) => {
let o = {};
o.order_id = ordObj.orderId;
o.pro_id = element.ID;
o.pro_name = element.NAME;
o.img = element.SMALL_IMG;
o.price = element.MARKET_PRICE;
o.proNum = 1;
o.star = 5;
o.comments = '';
o.imgList = [];
o.isAddImg = true;
this.evaList.push(o);
})
},
watch: {
evaList:{
handler: function (curVal, oldVal) {
curVal.forEach((ele ,i) => {
if (ele.imgList.length !== 4 && !ele.isAddImg)
ele.isAddImg = true;
if (ele.imgList.length === 4 && ele.isAddImg)
ele.isAddImg = false;
})
},
deep: true
}
},
mounted() {
},
}



